@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&family=Titillium+Web:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --mainColor: white;
  --mainColorLight: #5767aa;
  --secondaryColor: #DB2B39;
  --textColor: black;
}

body {
  font-family: 'Roboto', sans-serif;
  background-image: url(/precision_works_gp/static/media/capababilities_background.3de2f0dd.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
  position: relative;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7));
  z-index: -1;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
  opacity: 0.9;
}

.nav-logo {
  display: flex;
  align-items: center;
}

/* Set a background color specifically for .nav-logo within header */
header .nav-logo {
  background-color: transparent;
}

nav {
  display: flex;
  align-items: center;  
  transition: transform 0.3s ease-in-out;
  text-align: center; /* Center the navigation links */
}

nav a {
  display: block;
  padding: 20px 15px; /* Adjust padding */
  color: var(--textColor);
  font-size: 18px; /* Adjust font size */
  transition: color 0.3s ease-in-out;
  margin: 15px 0; /* Add margin for spacing */
}

nav a:hover {
  color: grey;
  text-decoration: underline;
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}

.nav-logo img {
  height: 80px;
  max-width: 300px;
  width: 100%;
}

.nav-email-link {
  background-color: rgb(115, 115, 164);
  text-align: right;
  vertical-align: middle;
  height: 30px;
}

.nav-email-link a {
  color: white;
  margin-right: 30px;
}

@media only screen and (max-width: 768px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    flex-direction: column;
    width: 100%;
    position: absolute;
    left: 0;
    background-color: var(--mainColor);
    transform: translateY(-100%);
    transition: transform 0.3s ease-in-out;
  }

  header .nav-open {
    transform: translateY(0);
    top: 0px;
    opacity: 1;
    background-color: rgba(255, 255, 255, 1); /* Darken the background color */
  }

  header .nav-open a {
    font-weight: bold;
    font-size: 25px;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 2rem; /* Increase font size */
    color: black; /* Set color to the main background color */
  }

  .nav-logo img {
    height: 60px;
    margin-right: 0;
    object-fit: contain;
  }

  .nav-logo {
    justify-content: left;
  }

  .nav-email-link a {
    vertical-align: 50%;
    margin-top: 10px;
  }
}

